<template>
  <div class="container-fluid h-custom">
    <div class="row d-flex justify-content-center align-items-center h-100">
      <div class="col-md-8 col-lg-12 col-xl-4 offset-xl-1">
        <form>
          <div class="divider d-flex align-items-center my-1">
            <p class="text-center fw-bold mx-3 mb-0">Acheter article</p>
          </div>

          <!-- Id marchand -->
          <div class="form-outline">
            <label class="form-label" for="Price">Id marchand</label>
            <select
              id="idmarchand"
              class="form-control form-control-lg"
              v-model="idmarchand"
            >
              <option
                v-for="(item, key) in ids"
                v-bind:value="item"
                v-bind:key="key"
              >
                {{ item.userid }}
              </option>
            </select>
          </div>
          <!-- name input -->
          <div class="form-outline">
            <label class="form-label" for="Name">Name :</label>
            <input
              type="label"
              id="Name"
              class="form-control form-control-lg"
              placeholder="Ex: Ordinateur"
              required="required"
              v-model="name"
            />
          </div>
          <!-- Quantity -->
          <div class="form-outline">
            <label class="form-label" for="quantity">Quantity</label>
            <input
              type="number"
              id="quantity"
              class="form-control form-control-lg"
              v-model="quantity"
            />
          </div>
          <!-- Price -->
          <div class="form-outline">
            <label class="form-label" for="Price">Price</label>
            <input
              type="number"
              id="Price"
              class="form-control form-control-lg"
              v-model="price"
            />
          </div>
          <!-- Taxes -->
          <div class="form-outline">
            <label class="form-label" for="Taxes">Taxes (%)</label>
            <input
              type="number"
              id="Taxes"
              class="form-control form-control-lg"
              v-model="taxes"
            />
          </div>
          <div class="text-center text-lg-start mt-2 pt-2">
            <button
              type="button"
              @click="acheter"
              class="btn btn-success btn-lg"
              style="padding-left: 2.5rem; padding-right: 2.5rem"
            >
              Acheter
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "TransactionComponent",
  data() {
    return {
      name: null,
      price: null,
      taxes: null,
      quantity: null,
      idmarchand: null,
      ids: [],
    };
  },
  mounted: function () {
    this.$store
      .dispatch("getMarchands")
      .then((result) => {
        this.ids = result.data;
      })
      .catch((error) => {
        console.log(error);
      });
  },
  computed: {},
  methods: {
    acheter() {
      const data = {
        userid: this.$store.getters.getTokens.userId,
        id: this.idmarchand.userid,
        name: this.name,
        unitprice: this.price,
        quantity: this.quantity,
        taxes: this.taxes,
      };
      this.$store
        .dispatch("transaction", data)
        .then((transaction) => {
          alert("Transaction effectué avec success !");
          console.log(transaction.data);
          this.$router.push("home");
        })
        .catch((error) => {
          console.log(error);
          alert("Erreur lors de la transaction !");
        });
    },
  },
};
</script>

