<template>
  <div class="container-fluid h-custom">
    <div class="row d-flex justify-content-center align-items-center h-100">
      <div class="col-md-8 col-lg-12 col-xl-4 offset-xl-1">
        <form>
          <div class="divider d-flex align-items-center my-1">
            <p class="text-center fw-bold mx-3 mb-0">Creer un nouveau client</p>
          </div>

          <!-- Fisrtname input -->
          <div class="form-outline">
            <label class="form-label" for="firstName">Prenom</label>
            <input
              type="label"
              id="firstName"
              class="form-control form-control-lg"
              placeholder="Ex: Lionel"
              required="required"
              v-model="firstname"
            />
          </div>
          <!-- Lastname input -->
          <div class="form-outline">
            <label class="form-label" for="lastName">Nom</label>
            <input
              type="label"
              id="lastName"
              class="form-control form-control-lg"
              placeholder="Ex: BONNAVENTURE"
              v-model="lastname"
            />
          </div>
          <div class="form-outline">
            <label class="form-label" for="country">Pays</label>
            <input
              type="label"
              id="country"
              class="form-control form-control-lg"
              placeholder="Ex: Senegal"
              v-model="country"
            />
          </div>

          <!-- Email input -->
          <div class="form-outline">
            <label class="form-label" for="mailAdress">Address Mail</label>
            <input
              type="email"
              id="mailAdress"
              class="form-control form-control-lg"
              placeholder="Ex: address@mail.com"
              v-model="email"
            />
          </div>
          <!-- Montant input -->
          <div class="form-outline">
            <label class="form-label" for="mailAdress">Montant</label>
            <input
              type="number"
              id="montant"
              class="form-control form-control-lg"
              v-model="montant"
            />
          </div>
          <!-- Password input -->
          <div class="form-outline">
            <label class="form-label" for="password">Mot de passe</label>
            <input
              type="password"
              id="password"
              class="form-control form-control-lg"
              placeholder="Entrez votre de passe"
              v-model="passwd"
            />
          </div>

          <div class="form-outline">
            <label class="form-label" for="confirmPassword"
              >Confirmer mot de passe</label
            >
            <input
              type="password"
              id="confirmPassword"
              class="form-control form-control-lg"
              placeholder="Entrez votre de passe"
              v-model="Confirmpasswd"
            />
          </div>
          <div class="text-center text-lg-start mt-2 pt-2">
            <button
              type="button"
              @click="signup"
              class="btn btn-success btn-lg"
              style="padding-left: 2.5rem; padding-right: 2.5rem"
              :disabled="!validData"
            >
              Créer utilisateur
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "CreateUserComponent",
  data() {
    return {
      firstname: null,
      lastname: null,
      country: null,
      email: null,
      passwd: null,
      Confirmpasswd: null,
      type: "client",
      isenabled: true,
      montant: null,
    };
  },
  computed: {
    validFName() {
      if (this.firstname != null) {
        return this.firstname != "";
      } else return false;
    },
    validLName() {
      if (this.lastname != null) {
        return this.lastname != "";
      } else return false;
    },
    validCountry() {
      if (this.country != null) {
        return this.country != "";
      } else return false;
    },
    validMail() {
      if (this.email != null) {
        return this.email != "";
      } else return false;
    },
    samePasswd() {
      if (this.passwd != null && this.Confirmpasswd != null) {
        if (this.passwd != "" && this.Confirmpasswd != "")
          return this.passwd == this.Confirmpasswd;
        else return false;
      } else return false;
    },
    validData() {
      return (
        this.validFName &&
        this.validLName &&
        this.validCountry &&
        this.validMail &&
        this.samePasswd
      );
    },
  },
  methods: {
    signup() {
      const user = {
        firstname: this.firstname,
        lastname: this.lastname,
        country: this.country,
        email: this.email,
        password: this.passwd,
        montant: this.montant,
        isenabled: true,
        type: "client",
      };
      this.$store
        .dispatch("signup", user)
        .then(() => {
          alert("Compte créeer avec success !");
          alert(
            "Vous pouvez à présent vous connecter au compte client et effectuer des achats"
          );
          this.$router.push("home");
        })
        .catch((error) => {
          console.log(error);
          alert("Erreur lors de la création du compte !");
        });
    },
    login() {
      this.$router.push("login");
    },
  },
};
</script>

