<script>
export default {
  data() {
    return {
      solde: null,
      dark: null,
    };
  },
  mounted: function () {
    if (this.$store.getters.getTokens.userId == -1) this.$router.push("login");
  },
  computed: {
    auth() {
      return this.$store.getters.isAuthentificate;
    },
  },
  methods: {
    sold() {
      this.$store
        .dispatch("balance", {
          userid: this.$store.getters.getTokens.userId,
        })
        .then((result) => {
          this.solde = result.data.balance;
        })
        .catch((error) => {
          console.log(error);
        });
      return this.solde;
    },
  },
};
</script>
