<template>
  <div class="container-fluid h-custom">
    <div class="row d-flex justify-content-center align-items-center h-100">
      <div class="col-md-8 col-lg-12 col-xl-4 offset-xl-1">
        <form>
          <div class="divider d-flex align-items-center my-1">
            <p class="text-center fw-bold mx-3 mb-0">Modifier mot de passe</p>
          </div>

          <!-- Fisrtname input -->
          <div class="form-outline">
            <label class="form-label" for="old">Ancien mot de passe</label>
            <input
              type="password"
              id="old"
              class="form-control form-control-lg"
              required="required"
              v-model="oldpasswd"
            />
          </div>
          <!-- Password input -->
          <div class="form-outline">
            <label class="form-label" for="password">Mot de passe</label>
            <input
              type="password"
              id="password"
              class="form-control form-control-lg"
              placeholder="Entrez votre de passe"
              v-model="passwd"
            />
          </div>

          <div class="form-outline">
            <label class="form-label" for="confirmPassword"
              >Confirmer mot de passe</label
            >
            <input
              type="password"
              id="confirmPassword"
              class="form-control form-control-lg"
              placeholder="Entrez votre de passe"
              v-model="Confirmpasswd"
            />
          </div>
          <div class="text-center text-lg-start mt-2 pt-2">
            <button
              type="button"
              @click="updatepassword"
              class="btn btn-success btn-lg"
              style="padding-left: 2.5rem; padding-right: 2.5rem"
              :disabled="!validData"
            >
              Modifier mot de passe
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "UpdatePasswordComponent",
  data() {
    return {
      oldpasswd: null,
      passwd: null,
      Confirmpasswd: null,
    };
  },
  computed: {
    validOld() {
      if (this.oldpasswd != null) {
        return this.oldpasswd != "";
      } else return false;
    },
    samePasswd() {
      if (this.passwd != null && this.Confirmpasswd != null) {
        if (this.passwd != "" && this.Confirmpasswd != "")
          return this.passwd == this.Confirmpasswd;
        else return false;
      } else return false;
    },
    validData() {
      return this.validOld && this.samePasswd;
    },
  },
  methods: {
    updatepassword() {
      const user = {
        id: this.$store.getters.getTokens.userId,
        oldpassword: this.oldpasswd,
        password: this.passwd,
      };
      this.$store
        .dispatch("updatePassword", user)
        .then(() => {
          alert(
            "Mot de passe modifié avec success ! veuillez vous reconnecter"
          );
          this.$router.push("login");
        })
        .catch((error) => {
          alert("Echec de la modification du mot de passe !");
          console.log(error);
        });
    },
  },
};
</script>

