<template>
  <div class="container d-flex flex-column">
    <div class="row align-items-center justify-content-center min-vh-100 g-0">
      <div class="col-12 col-md-8 col-lg-4 border-top border-3 border-success">
        <div class="card shadow-sm">
          <div class="card-body">
            <div class="mb-4">
              <h5>Mot de passe oublié?</h5>
              <p class="mb-2">
                Entrez votre adresse mail pour renitialiser votre mot de passe
              </p>
            </div>
            <form>
              <div class="mb-3">
                <label for="email" class="form-label">Email</label>
                <input
                  type="email"
                  id="email"
                  class="form-control"
                  name="email"
                  placeholder="Entrer votre mail"
                  required=""
                  v-model="email"
                />
              </div>
              <div class="mb-3 d-grid">
                <button type="button" class="btn btn-success" @click="reset">
                  Renitialiser mot de passe
                </button>
              </div>
              <span
                >Vous n'avez pas de compte?
                <a href="#" @click="create">Créer compte</a></span
              >
            </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "PasswordForgetComponent",
  data() {
    return {
      email: null,
    };
  },
  methods: {
    reset() {
      this.$store.commit("setResetMail", this.email);
      console.log(this.$store.getters.getResetMail);
      this.$store.dispatch("resetPassword").then((result) => {
        if (result) this.$router.push("reset");
      });
    },
    create() {
      this.$router.push("signup");
    },
  },
};
</script>
<style>
@import "~bootstrap/dist/css/bootstrap.css";
</style>
