<template>
  <div class="container-fluid h-custom">
    <div class="row d-flex justify-content-center align-items-center h-100">
      <div class="col-md-9 col-lg-6 col-xl-5">
        <img
          src="../assets/vue-js-development.jpeg"
          class="img-fluid"
          alt="Sample image"
        />
      </div>
      <div class="col-md-8 col-lg-12 col-xl-4 offset-xl-1">
        <form>
          <div class="divider d-flex align-items-center my-1">
            <p class="text-center fw-bold mx-3 mb-0">Inscription</p>
          </div>

          <!-- Fisrtname input -->
          <div class="form-outline">
            <label class="form-label" for="firstName">Prenom</label>
            <input
              type="label"
              id="firstName"
              class="form-control form-control-lg"
              placeholder="Ex: Lionel"
              required="required"
              v-model="firstname"
            />
          </div>
          <!-- Lastname input -->
          <div class="form-outline">
            <label class="form-label" for="lastName">Nom</label>
            <input
              type="label"
              id="lastName"
              class="form-control form-control-lg"
              placeholder="Ex: BONNAVENTURE"
              v-model="lastname"
            />
          </div>
          <div class="form-outline">
            <label class="form-label" for="country">Pays</label>
            <input
              type="label"
              id="country"
              class="form-control form-control-lg"
              placeholder="Ex: Senegal"
              v-model="country"
            />
          </div>

          <!-- Email input -->
          <div class="form-outline">
            <label class="form-label" for="mailAdress">Address Mail</label>
            <input
              type="email"
              id="mailAdress"
              class="form-control form-control-lg"
              placeholder="Ex: address@mail.com"
              v-model="email"
            />
          </div>

          <!-- Password input -->
          <div class="form-outline">
            <label class="form-label" for="password">Mot de passe</label>
            <input
              type="password"
              id="password"
              class="form-control form-control-lg"
              placeholder="Entrez votre de passe"
              v-model="passwd"
            />
          </div>

          <div class="form-outline">
            <label class="form-label" for="confirmPassword"
              >Confirmer mot de passe</label
            >
            <input
              type="password"
              id="confirmPassword"
              class="form-control form-control-lg"
              placeholder="Entrez votre de passe"
              v-model="Confirmpasswd"
            />
          </div>
          <div class="text-center text-lg-start mt-2 pt-2">
            <button
              type="button"
              @click="signup"
              class="btn btn-success btn-lg"
              :disabled="!validData"
              style="padding-left: 2.5rem; padding-right: 2.5rem"
            >
              S'inscrire
            </button>
            <p class="small fw-bold mt-2 pt-1 mb-0">
              Vous avez déjà un compte?
              <a href="#!" class="link-danger" @click="login">Se connecter</a>
            </p>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "SignupComponent",
  data() {
    return {
      firstname: null,
      lastname: null,
      country: null,
      email: null,
      passwd: null,
      Confirmpasswd: null,
    };
  },
  computed: {
    validFName() {
      if (this.firstname != null) {
        return this.firstname != "";
      } else return false;
    },
    validLName() {
      if (this.lastname != null) {
        return this.lastname != "";
      } else return false;
    },
    validCountry() {
      if (this.country != null) {
        return this.country != "";
      } else return false;
    },
    validMail() {
      if (this.email != null) {
        return this.email != "";
      } else return false;
    },
    samePasswd() {
      if (this.passwd != null && this.Confirmpasswd != null) {
        if (this.passwd != "" && this.Confirmpasswd != "")
          return this.passwd == this.Confirmpasswd;
        else return false;
      } else return false;
    },
    validData() {
      return (
        this.validFName &&
        this.validLName &&
        this.validCountry &&
        this.validMail &&
        this.samePasswd
      );
    },
  },
  methods: {
    signup() {
      const user = {
        firstname: this.firstname,
        lastname: this.lastname,
        country: this.country,
        email: this.email,
        password: this.passwd,
      };
      this.$store
        .dispatch("signup", user)
        .then(() => {
          //TODO: Ajouter une redirection sur la page d'activation du compte
          alert("Compte créeer avec success !");
          this.$router.push("active");
        })
        .catch((error) => {
          console.log(error);
          alert("Erreur lors de la création du compte !");
        });
    },
    login() {
      this.$router.push("login");
    },
  },
};
</script>

