<template>
  <LoginComponent />
</template>

<script>
// @ is an alias to /src
import LoginComponent from "@/components/LoginComponent.vue";
export default {
  name: "LoginView",
  components: {
    LoginComponent,
  },
  mounted: function () {
    let data = { userId: -1, token: "" };
    this.$store.commit("isAuthentificate", false);
    if (this.$store.getters.getTokens.userId != -1)
      this.$store.commit("setTokens", data);
  },
};
</script>
