import Vue from 'vue'
import Vuex from 'vuex'
const utilitaire = require('../utils/utils');
Vue.use(Vuex)
const axios = require('axios');
const instance = axios.create({
  baseURL: "https://apiepay.herokuapp.com/api/"
  //"https://apiepay.herokuapp.com/api/" production api
  // http://localhost:5000/api/ :: developpement api
})

export default new Vuex.Store({
  state: {
    isAuthentificate: false,
    confirmationCode: null,
    id: null,
    resetMail: null,
    resetCode: null,
    token: {
      userId: -1,
      token: '',
      type: '',
      balance: '',
    },
    profil: {
    },
  },
  getters: {
    getProfil(state) {
      return state.profil;
    },
    isAuthentificate(state) {
      return state.isAuthentificate;
    },
    getTokens(state) {
      return state.token;
    },
    getconfimationCode(state) {
      return state.confirmationCode;
    },
    getresetCode(state) {
      return state.resetCode;
    },
    getId(state) {
      return state.id;
    },
    getResetMail(state) {
      return state.resetMail;
    }
  },
  mutations: {
    isAuthentificate(state, etat) {
      state.isAuthentificate = etat;
    },
    setId(state, id) {
      state.id = id;
    },
    setProfil(state, profil) {
      state.profil = profil;
    },
    setResetMail(state, mail) {
      state.resetMail = mail;
    },
    setTokens(state, newToken) {
      state.token = newToken;
    },
    setHeaders(state) {
      instance.defaults.headers.common['Authorization'] = state.token.token;
    },
    setconfirmationCode(state, code) {
      state.confirmationCode = code;
    },
    setresetCode(state, code) {
      state.resetCode = code;
    }
  },
  actions: {
    login({ commit }, donnees) {
      return new Promise((resolve, reject) => {
        instance.post('/user/signin', donnees)
          .then((result) => {
            commit('setTokens', result.data)
            commit('setHeaders');
            resolve(result);
          })
          .catch((error => {
            reject(error);
          }))
      });
    },
    signup({ commit }, donnees) {
      commit;
      return new Promise((resolve, reject) => {
        instance.post('/user/signup', donnees)
          .then((result) => {
            let validCode = utilitaire.makeCode(7)
            commit('setconfirmationCode', validCode);
            commit('setId', result.data.result.userid);
            instance.post('/user/sendConfirm', { email: donnees.email, code: this.getters.getconfimationCode })
            resolve(result);
          })
          .catch((error) => {
            reject(error);
          })
      })
    },
    transaction({ commit }, donnees) {
      commit;
      instance.defaults.headers.common['Authorization'] = this.getters.getTokens.token;
      return new Promise((resolve, reject) => {
        instance.post('/article/addarticle', donnees)
          .then((result) => {
            resolve(result);
          })
          .catch((error) => {
            reject(error);
          })
      })
    },
    balance({ commit }, donnees) {
      commit;
      instance.defaults.headers.common['Authorization'] = this.getters.getTokens.token;
      return new Promise((resolve, reject) => {
        instance.post('/account/balance', donnees)
          .then((result) => {
            resolve(result);
          })
          .catch((error) => {
            reject(error);
          })
      })
    },
    active({ commit }, donnees) {
      if (donnees.code == this.getters.getconfimationCode) {
        commit;
        return new Promise((resolve, reject) => {
          const data = { id: this.getters.getId };
          instance.post('/profile/activeAccount', data)
            .then((result) => {
              commit('setconfirmationCode', "");
              resolve(result)
            })
            .catch((error) => {
              reject(error);
            })
        })
      } else
        alert("Code de confirmation incorrect");
    },
    resetPassword({ commit }) {
      commit;
      let codeConfirm = utilitaire.makeCode(7);
      commit('setconfirmationCode', codeConfirm);
      let data = { email: this.getters.getResetMail, code: codeConfirm };
      return new Promise((resolve, reject) => {
        instance.post('/user/getUserByMail', data)
          .then((user) => {
            resolve(user)
          })
          .catch((error) => {
            console.log(error)
            reject(error)
          });
      })
    },
    reset({ commit }, donnees) {
      if (donnees.code == this.getters.getconfimationCode) {
        commit;
        return new Promise((resolve, reject) => {
          const data = { email: this.getters.getResetMail };
          instance.post('/user/resetPassword', data)
            .then((result) => {
              commit('setconfirmationCode', "");
              commit('setResetMail', "");
              resolve(result)
            })
            .catch((error) => {
              reject(error);
            })
        })
      } else
        console.log("Code non cohérant! ");
    },
    getMarchands({ commit }) {
      commit;
      return new Promise((resolve, reject) => {
        instance.get('/account/getmarchands')
          .then((result) => {
            resolve(result)
          })
          .catch((error) => {
            reject(error);
          })
      })
    },
    updatePassword({ commit }, donnees) {
      commit;
      return new Promise((resolve, reject) => {
        instance.post('/user/updatepassword', donnees)
          .then((result) => {
            resolve(result)
          })
          .catch((error) => {
            reject(error);
          })
      })
    },
    getUserProfil({ commit }, donnees) {
      commit;
      return new Promise((resolve, reject) => {
        instance.get('/profile/getuserprofile/' + donnees)
          .then((result) => {
            commit("setProfil", result.data);
            resolve(result)
          })
          .catch((error) => {
            reject(error);
          })
      })
    },
    editProfil({ commit }, donnees) {
      commit;
      return new Promise((resolve, reject) => {
        instance.put('/profile/editprofile/', donnees)
          .then((result) => {
            resolve(result)
          })
          .catch((error) => {
            reject(error);
          })
      })
    }
  },
  modules: {
  }
})
