<template>
  <div class="container d-flex flex-column">
    <div class="row align-items-center justify-content-center min-vh-100 g-0">
      <div class="col-12 col-md-8 col-lg-4 border-top border-3 border-success">
        <div class="card shadow-sm">
          <div class="card-body">
            <div class="mb-4">
              <h5>Saisir le code de confirmation</h5>
              <p class="mb-2">Veuillez saisir le code reçu sur votre mail</p>
            </div>
            <form>
              <div class="mb-3">
                <label for="code" class="form-label">Code</label>
                <input
                  type="text"
                  id="code"
                  class="form-control"
                  name="code"
                  placeholder="Entrer le code à 6 chiffre"
                  required=""
                  v-model="code"
                />
              </div>
              <div class="mb-3 d-grid">
                <button
                  class="btn btn-success"
                  type="button"
                  @click="active"
                  v-if="type == 'active'"
                >
                  Activer compte
                </button>
                <button
                  class="btn btn-success"
                  type="button"
                  @click="reset"
                  v-if="type == 'reset'"
                >
                  Renitialiser mot de passe
                </button>
              </div>
              <span
                >Vous n'avez pas reçu de mail?
                <a href="#" @click="changerAdresse"
                  >Changer d'adresse mail</a
                ></span
              >
            </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "ConfirmationCodeComponent",
  props: { type: String },
  data() {
    return {
      code: null,
    };
  },
  methods: {
    active() {
      this.$store
        .dispatch("active", { code: this.code })
        .then((result) => {
          if (result) this.$router.push("home");
        })
        .catch((error) => console.log(error));
    },
    reset() {
      this.$store
        .dispatch("reset", { code: this.code })
        .then((result) => {
          if (result) this.$router.push("home");
        })
        .catch((error) => console.log(error));
    },
    changerAdresse() {
      if (this.type == "active") this.$router.push("signup");
      else this.$router.push("forget");
    },
  },
};
</script>
<style>
@import "~bootstrap/dist/css/bootstrap.css";
</style>
