<template>
  <div class="home">
    <SignupComponent />
  </div>
</template>

<script>
// @ is an alias to /src
import SignupComponent from "@/components/SignupComponent.vue";

export default {
  name: "SignupView",
  components: {
    SignupComponent,
  },
  mounted: function () {
    let data = { userId: -1, token: "" };
    this.$store.commit("isAuthentificate", false);
    if (this.$store.getters.getTokens.userId != -1)
      this.$store.commit("setTokens", data);
  },
};
</script>
