import Vue from 'vue'
import VueRouter from 'vue-router'
import LoginView from '../views/LoginView.vue'
import SignupView from '../views/SignupView.vue'
import HomeView from '../views/HomeView.vue'
import DocumentationView from '../views/DocumentationView.vue'
import ActiveAccountView from '../views/ActiveAccountView.vue'
import PasswordForgetView from '../views/PasswordForgetView.vue'
import ResetPasswordConfirm from '../views/ResetPasswordConfirm.vue'
import SandBoxView from '../views/SandBoxView.vue';
import UpdatePasswordView from '../views/UpdatePasswordView.vue';
import ProfilInfoView from '../views/ProfilInfoView.vue';
import UpdateProfilView from '../views/UpdateProfilView.vue';

Vue.use(VueRouter)

const routes = [
  {
    path: '/login',
    name: 'login',
    component: LoginView
  },
  {
    path: '/signup',
    name: 'signup',
    component: SignupView
  },
  {
    path: '/home',
    name: 'home',
    component: HomeView
  },
  {
    path: '/active',
    name: 'active',
    component: ActiveAccountView
  },
  {
    path: '/docs',
    name: 'docs',
    component: DocumentationView
  },
  {
    path: '/forget',
    name: 'forget',
    component: PasswordForgetView
  },
  {
    path: '/reset',
    name: 'reset',
    component: ResetPasswordConfirm
  },
  {
    path: '/sandbox',
    name: 'sandbox',
    component: SandBoxView
  },
  {
    path: '/updatepassword',
    name: 'updatepassword',
    component: UpdatePasswordView
  },
  {
    path: '/updateprofil',
    name: 'updateprofil',
    component: UpdateProfilView
  },
  {
    path: '/profil',
    name: 'profil',
    component: ProfilInfoView
  },
]

const router = new VueRouter({
  routes
})

export default router
