<template>
  <div class="container-fluid h-custom">
    <div class="row d-flex justify-content-center align-items-center h-100">
      <div class="col-md-9 col-lg-6 col-xl-5">
        <img
          src="../assets/vue-js-development.jpeg"
          class="img-fluid"
          alt="Sample image"
        />
      </div>
      <div class="col-md-8 col-lg-6 col-xl-4 offset-xl-1">
        <form>
          <div class="divider d-flex align-items-center my-4">
            <p class="text-center fw-bold mx-3 mb-0">Connexion</p>
          </div>

          <!-- Email input -->
          <div class="form-outline mb-4">
            <label class="form-label" for="form3Example3">Address Mail</label>
            <input
              type="email"
              id="form3Example3"
              class="form-control form-control-lg"
              placeholder="Ex: address@mail.com"
              v-model="login"
            />
          </div>

          <!-- Password input -->
          <div class="form-outline mb-3">
            <label class="form-label" for="form3Example4">Mot de passe</label>
            <input
              type="password"
              id="form3Example4"
              class="form-control form-control-lg"
              placeholder="Entrez votre de passe"
              v-model="passwd"
            />
          </div>

          <div class="d-flex justify-content-between align-items-center">
            <!-- Checkbox -->
            <div class="form-check mb-0">
              <input
                class="form-check-input me-2"
                type="checkbox"
                value=""
                id="form2Example3"
              />
              <label class="form-check-label" for="form2Example3">
                Se souvenir de moi
              </label>
            </div>
            <a href="#!" class="text-body" @click="forgetPassword"
              >Mot de passe oublié?</a
            >
          </div>

          <div class="text-center text-lg-start mt-4 pt-2">
            <button
              type="button"
              @click="connect"
              class="btn btn-success btn-lg"
              style="padding-left: 2.5rem; padding-right: 2.5rem"
            >
              Se connecter
            </button>
            <p class="small fw-bold mt-2 pt-1 mb-0">
              Vous n'avez pas de compte?
              <a href="#!" class="link-danger" @click="register">S'inscrire</a>
            </p>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "LoginComponent",
  data() {
    return {
      login: null,
      passwd: null,
    };
  },
  methods: {
    isConnect(etat) {
      this.$store.commit("isAuthentificate", etat);
    },
    forgetPassword() {
      this.$router.push("forget");
    },
    connect() {
      this.$store
        .dispatch("login", {
          email: this.login,
          password: this.passwd,
        })
        .then((result) => {
          console.log(result.data);
          this.isConnect(true);
          this.$router.push("home");
        })
        .catch((error) => {
          alert("Paire login / mot de passe incorrete");
          console.log(error);
        });
    },
    register() {
      this.$router.push("signup");
    },
  },
};
</script>

