<template>
  <div class="container-fluid h-custom">
    <div class="row d-flex justify-content-center align-items-center h-100">
      <div class="col-md-8 col-lg-12 col-xl-4 offset-xl-1">
        <form>
          <div class="divider d-flex align-items-center my-1">
            <p class="text-center fw-bold mx-3 mb-0">Creer un nouveau client</p>
          </div>

          <!-- Fisrtname input -->
          <div class="form-outline">
            <label class="form-label" for="firstName">Prenom</label>
            <input
              type="label"
              id="firstName"
              class="form-control form-control-lg"
              required="required"
              value="profile.firstname"
              v-model="profile.firstname"
            />
          </div>
          <!-- Lastname input -->
          <div class="form-outline">
            <label class="form-label" for="lastName">Nom</label>
            <input
              type="label"
              id="lastName"
              class="form-control form-control-lg"
              value="profile.lastname"
              v-model="profile.lastname"
            />
          </div>
          <div class="form-outline">
            <label class="form-label" for="age">Age</label>
            <input
              type="number"
              id="age"
              class="form-control form-control-lg"
              value="profile.age"
              v-model="profile.age"
            />
          </div>
          <div class="form-outline">
            <label class="form-label" for="country">Pays</label>
            <input
              type="label"
              id="country"
              class="form-control form-control-lg"
              value="profile.country"
              v-model="profile.country"
            />
          </div>
          <div class="text-center text-lg-start mt-2 pt-2">
            <button
              type="button"
              @click="updateProfil"
              class="btn btn-success btn-lg"
              style="padding-left: 2.5rem; padding-right: 2.5rem"
            >
              Modifier profil
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "UpdateProfilComponent",
  data() {
    return {
      profile: null,
    };
  },
  mounted: function () {
    this.$store
      .dispatch("getUserProfil", this.$store.getters.getTokens.userId)
      .then((result) => {
        this.profile = result.data;
      })
      .catch((error) => {
        console.log(error);
      });
  },
  computed: {},
  methods: {
    updateProfil() {
      const user = {
        id: this.$store.getters.getTokens.userId,
        firstname: this.profile.firstname,
        lastname: this.profile.lastname,
        country: this.profile.country,
        age: this.profile.age,
      };
      this.$store
        .dispatch("editProfil", user)
        .then(() => {
          alert("Profil Modifié !");
          this.$router.push("profil");
        })
        .catch((error) => {
          console.log(error);
          alert("Erreur lors de la Modification !");
        });
    },
  },
};
</script>

