          <h1 class="text-start">Signin</h1>
        </div>
        <p>
          Cette route nous permet de nous connecter à notre compte et au
          dashboard_spa<br />
          Elle prend un login et un mot de passe , pour fournir en retour un
          tokens d'une validité de 24 quand les informations sont bonnes<br />
        </p>
        <h3>Routes signin</h3>
        <code>router.post('/user/signin', userCtrl.signin); </code>
        <h3>Code de l'API</h3>
        <br />
        <pre>
          <code>
  exports.signin = (req, res) => {
  User.findOne({ where: { email: req.body.email } })
    .then(user => {
      if (!user) {
        return res.status(401).json({ message: 'Paire email/mot de passe incorrecte' });
      }
      bcrypt.compare(req.body.password, user.password)
        .then(valid => {
          if (!valid) {
            return res.status(401).json({ message: 'Paire email/mot de passe incorrecte' });
          }
          res.status(200).json({
            userId: user.userid,
            token: jwt.sign(
              { userId: user.userid },
              'EPAY_TOKEN_SECRET',
              { expiresIn: '24h' }
            )
          });

        })
        .catch(error => res.status(500).json({ error }));
    })
    .catch(error => res.status(500).json({ error }));
};
          </code>
        </pre>
      </div>
    </div>
    <!-- Partie signup !-->
    <div class="row" v-if="type == 'signup'">
      <div class="row">
        <h1 class="text-start">Signup</h1>
      </div>
      <div class="row text-start">
        <p>
          Cette route nous permet de creer un compte client ou marchand<br />
          Par defaut, elle crée des comptes pour les marchands en prennant en
          entrée :<br />
          -Le nom <br />
          -Le prenom <br />
          -Le pays <br />
          -Le mail/teléphone <br />
          -Le mot de passe <br />
          -La confirmation du mot de passe <br />
          Après la création du compte , un mail est envoyé à au marchand pour
          activer son compte.<br />
          Le compte du client est automatiquement activé.
        </p>
        <h3>Routes signup</h3>
        <code>router.post('/user/signup', userCtrl.signup); </code>
        <h3>Code de l'API</h3>
        <br />
        <pre>
          <code>
  exports.signup = (req, res) => {
  bcrypt
    .hash(req.body.password, 10)
    .then((hash) => {
      const user = User.build({
        phone: req.body.phone,
        email: req.body.email,
        password: hash,
      });
      user
        .save()
        .then((result) => {
          const profile = Profile.build({
            userid: result.userid,
            firstname: req.body.firstname,
            lastname: req.body.lastname,
            country: req.body.country,
            age: req.body.age,
            isenabled: false,
          });
          profile
            .save()
            .then((resultProfile) => {
              let typeUser = "Marchand";
              if (req.body.type != null) typeUser = req.body.type;
              const account = Account.build({
                userid: result.userid,
                balance: 0,
                currency: "XOF",
                type: typeUser,
              });
              account
                .save()
                .then((resultAccount) =>
                  res.status(201).json({ result, resultAccount, resultProfile })
                )
                .catch((error) => res.status(404).json(error));
            })
            .catch((err) => res.status(401).json(err));
        })
        .catch((err) => res.status(401).json(err));
    })
    .catch((err) => res.status(500).json(err));
};
          </code>
        </pre>
