      <div class="row">
        <h1 class="text-start">Sold Account</h1>
      </div>
      <div class="row text-start">
        <p>
          Cette route nous permet de solder le compte d'un client pour pouvoir
          effectuer des achats<br />
        </p>
        <h3>Routes sold Account</h3>
        <p>
          Cette route prends un middleware qui permet de verifier le tokens de
          l'utilisteur et securiser l'opération
        </p>
        <code
          >router.post('/account/sold/:userid', auth, accountCtrl.soldAccount);
        </code>
        <br />
        <h3>Code de l'API</h3>
        <br />
        <pre>
        <code>
  exports.soldAccount = (req, res) => {
  const userid = req.params.userid;
  Account.findOne({
    where: {
      userid: userid,
    },
  })
    .then((account) => {
      let nouveauSolde =
        parseFloat(account.balance) + parseFloat(req.body.montant);
      Account.update(
        { balance: nouveauSolde },
        {
          where: {
            userid: account.userid,
            type: "client",
          },
        }
      )
        .then((result) => {
          if (result == 1)
            res.status(200).json({ Message: "Solde credité avec success !" });
          else
            res.status(500).json({ Message: "Impossible de solder ce compte" });
        })
        .catch((err) => res.status(404).json(err));
    })
    .catch((err) => res.status(404).json(err));
};
        </code>
