    <!-- Partie add article !-->
    <div class="row" v-if="type == 'addarticle'">
      <div class="row">
        <h1 class="text-start">Add article</h1>
      </div>
      <div class="row text-start">
        <p>
          Cette route permet au client d'effectuer des achats sur le site du
          marchand<br />
          Il doit au préalable avoir les fonds nécessaires pour effectuer la
          transaction
        </p>
        <h3>Routes add article</h3>
        <p>
          Cette route prends un middleware qui permet de verifier le tokens de
          l'utilisteur et securiser l'opération
        </p>
        <code
          >router.post('/article/addarticle', auth, articleCtrl.addArticle);
        </code>
        <br />
        <h3>Code de l'API</h3>
        <br />
        <pre>
          <code>
  exports.addArticle = (req, res) => {
  let transactionStatus = "waiting";
  let totalAmmount =
    parseFloat(req.body.unitprice) * parseFloat(req.body.quantity);
  const finalAmount =
    totalAmmount +
    utils.calculTaxes(totalAmmount, req.body.taxes) +
    utils.isDefine(req.body.feeas) +
    utils.isDefine(req.body.adeedfeeas);
  Account.findOne({
    where: {
      userid: utils.getUserId(req),
      type: "client",
    },
  })
    .then((accountClient) => {
      if (accountClient.balance >= finalAmount) {
        transactionStatus = "OK";
        let nouveauSolde =
          parseFloat(accountClient.balance) - parseFloat(finalAmount);
        Account.update(
          { balance: nouveauSolde },
          { where: { userid: utils.getUserId(req), type: "client" } }
        )
          .then(() => {
            console.log("Solde client debité");
            Account.findOne({
              where: { userid: process.env.MARCHAND, type: "Marchand" },
            })
              .then((accountMarchand) => {
                let nouveauSoldeMarchand =
                  accountMarchand.balance + finalAmount;
                Account.update(
                  { balance: nouveauSoldeMarchand },
                  {
                    where: {
                      userid: process.env.MARCHAND,
                      type: "Marchand",
                    },
                  }
                )
                  .then(() => {
                    const transaction = Transaction.build({
                      userid: utils.getUserId(req),
                      status: transactionStatus,
                      amount: totalAmmount,
                      currency: "XOF",
                      identifier: req.body.identifier,
                      taxes: utils.isDefine(req.body.taxes),
                      feeas: utils.isDefine(req.body.feeas),
                      adeedfeeas: utils.isDefine(req.body.adeedfeeas),
                      finalamount: finalAmount,
                    });
                    transaction
                      .save()
                      .then((transaction_) => {
                        const article = Article.build({
                          name: req.body.name,
                          idtransaction: transaction_.idtransaction,
                          unitprice: req.body.unitprice,
                          quantity: req.body.quantity,
                          totalprice: totalAmmount,
                        });
                        article
                          .save()
                          .then(() => {
                            res
                              .status(200)
                              .json({
                                Message:
                                  "Etat de la transaction : " +
                                  transaction_.status,
                              });
                          })
                          .catch((err) => res.status(404).json(err));
                      })
                      .catch((err) => res.status(404).json(err));
                  })
                  .catch(() =>
                    res
                      .status(404)
                      .json({
                        Message:
                          "Erreur lors de l'ajout des fonds dans le compte marchand",
                      })
                  );
              })
              .catch(() =>
                res
                  .status(404)
                  .json({ Message: "Erreur du débit du compte client" })
              );
          })
          .catch(() =>
            res
              .status(404)
              .json({ Message: "Erreur du débit du compte client" })
          );
      } else res.status(404).json({ Message: "Solde insuffisant !" });
    })
    .catch((error) => res.status(404).json(error));
};
          </code>
