<template>
  <div>
    <div class="gauche">
      <SideBarComponent v-on:changerEtat="test"></SideBarComponent>
    </div>
    <div class="droite">
      <DocsContentComponent :type="etat" />
    </div>
  </div>
</template>
<script>
import SideBarComponent from "@/components/SideBarComponent.vue";
import DocsContentComponent from "@/components/DocsContentComponent.vue";
export default {
  name: "DocumentationView",
  components: {
    SideBarComponent,
    DocsContentComponent,
  },
  data() {
    return {
      etat: "introduction",
    };
  },
  mounted: function () {
    if (this.$store.getters.getTokens.userId == -1) this.$router.push("login");
  },
  methods: {
    test(val) {
      this.etat = val;
    },
  },
};
</script>
